import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTicket,faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import "../css/header.css";
function HeaderNav() {
  const [searchBar , setSearchBar] = useState(false);
  const [scrollNav , setScrollNav] = useState(false);
  const [inputValue, setInputValue]= useState('');
  const navigate = useNavigate();
  const toggleSearchBar = ()=>{
    setSearchBar(!searchBar);
  }
  const searchText=(event)=>{
    setInputValue(event.target.value);
  }
  const searchValue = (event)=>{
    event.preventDefault();
       navigate(`/toyproject-movieinfo/search/${inputValue}`);
      setInputValue("");

  }
  const handleScrollNav = ()=> {
    if(window.scrollY > 0) {
      setScrollNav(true);
    }else{
      setScrollNav(false);
    }
  };

  useEffect(()=> {
    window.addEventListener('scroll',handleScrollNav);
    return ()=> {
      window.removeEventListener('scroll',handleScrollNav);
    }
  },[]);

  return (
    <div>
      <nav className={`navbar ${scrollNav ? "scroll" : ""}`}>
        <div className="navbar_logo">
          <FontAwesomeIcon icon={faTicket} className="linklogoimg" />
          <Link
            to="/toyproject-movieinfo/"
            className="linklogo"
          >
            
          </Link>
        </div>

        <ul className="navbar_menu">
          <li>
            <Link className="linkmenu"></Link>
          </li>
          <li>
            <Link className="linkmenu"></Link>
          </li>
          <li>
            <Link className="linkmenu"></Link>
          </li>
        </ul>
        <div className={`navbar_search ${searchBar ? "active" : ""}`}>
          <form onSubmit={searchValue}>
            <FontAwesomeIcon
              icon={faMagnifyingGlass}
              className="searchBar_icon"
              onClick={toggleSearchBar}
            />
            {searchBar && (
              <input
                type="text"
                className="searchBar_input"
                value={inputValue}
                onChange={searchText}
                placeholder=" "
                onBlur={toggleSearchBar}
                autoFocus
              />
            )}
          </form>
        </div>
      </nav>
    </div>
  );
}

export default HeaderNav;
