import { useParams } from "react-router-dom";
import "../css/search.css"
import axios from "axios";
import { useState,useEffect } from "react";
import Paging from "./paging";

function Search() {
  const TMDBAPI = "460b0a97023202f5144501426e8e44fc";
  const [searchData,setSearchData] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPage,setTotalPage]=useState("");
  const { name } = useParams();

  useEffect(()=> {
    const printData = async(query,page) => {
      try{
        const response = await axios.get(
          `https://api.themoviedb.org/3/search/movie?query=${query}&api_key=${TMDBAPI}&language=ko-KR&page=${page}`
        );
       setSearchData(response.data.results);
       setTotalPage(response.data.total_pages);
    
      } catch (error) {
          console.error("Error   ",error);
      }
    };
 printData(name,currentPage);

  },[name,currentPage])

    const handlePageChange = (pageNumber) => {
      setCurrentPage(pageNumber);
    };

    //     
    useEffect(() => {
      setCurrentPage(1);
    }, [name]);


  return (
    <div className="searchcontainer">
      <h2>{name}</h2>
      {searchData &&
        searchData.map((movie, index) => (
          <div key={index} className="searchwrapper">
            <div className="searchposter">
              <img
                src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                alt={"  "}
              ></img>
            </div>
            <div className="detail">
              <div className="detailtitle">
                <p>
                  {movie.title} ({movie.original_title})
                </p>
                <span>{movie.release_date}</span>
              </div>
              <div className="detailoverview">
                <p>{movie.overview}</p>
                <p>{movie.vote_average ? movie.vote_average.toFixed(1) : '  '} / (10.0)</p>
              </div>
            </div>
          </div>
        ))}
 
      <Paging onPageChange={handlePageChange} totalPage={totalPage} />
    </div>
  );
}

export default Search;