import { useEffect, useState } from "react";
import "../css/trailer.css"
import axios from "axios";
import { useSelector } from "react-redux";
function Trailer() {
  let boxofficeList = useSelector((state) => state.boxOfficeRank);

  const [boxofficeOne, setBoxofficeOne] = useState("");
  const [trailerLink, setTrailerLink] = useState(null);
  const [tailerPoster,setTailerPoster]= useState(null);
  const TMDBAPI = "460b0a97023202f5144501426e8e44fc";
useEffect(() => {
  if (
    boxofficeList &&
    boxofficeList.boxOfficeRankData &&
    boxofficeList.boxOfficeRankData.length > 0
  ) {
    setBoxofficeOne(boxofficeList.boxOfficeRankData[0].movieNm); // 1
    trailerData(boxofficeOne, setTrailerLink, setTailerPoster, TMDBAPI);
  }
}, [boxofficeList]);


return (
  <div
    style={{
      backgroundImage: `url(${tailerPoster})`,
      backgroundSize: "60% 100%",
      backgroundPosition: "left",
      width: "100%",
      height: "90vh",
    }}
  >
    <div className="youtubetitle">
      <h2>{boxofficeOne}</h2>
    </div>
    <div className="youtube">
      <YouTubeVideo videoId={trailerLink}></YouTubeVideo>
    </div>
  </div>
);

}

async function trailerData(trailerMovie,setTrailerLink,setTailerPoster,TMDBAPI) {
  try{
    const {youtubeId,moivePosterUrl} = await getMovieTrailer(trailerMovie,TMDBAPI);
    setTailerPoster(moivePosterUrl);
    setTrailerLink(youtubeId); // 1  
    
  }catch(error){
    console.error("TMDB  :" ,error);
    return null;
  }
}

async function getMovieTrailer(trailerMovie, TMDBAPI) {
  try {
    const searchUrl = `https://api.themoviedb.org/3/search/movie?api_key=${TMDBAPI}&query=${encodeURIComponent(
      trailerMovie
    )}&language=ko-KR`;
   
    let searchResponse = await axios.get(searchUrl);


    if(searchResponse.data.results.length===0){
      const seriesMoive = trailerMovie.replace(/(\S)\s*$/, " $1");
      const seriesUrl = `https://api.themoviedb.org/3/search/movie?api_key=${TMDBAPI}&query=${encodeURIComponent(
        seriesMoive
      )}&language=ko-KR`;

       searchResponse = await axios.get(seriesUrl);

      if (searchResponse.data.results.length === 0) {
        console.error("   ");
        return null;
      }

      
    }

    const movieId = searchResponse.data.results[0].id; //     
    const moviePoster = searchResponse.data.results[0].poster_path;   
    
    const moivePosterUrl = moviePoster ? `https://image.tmdb.org/t/p/w500/${moviePoster}`:null;


    const trailerUrl = `https://api.themoviedb.org/3/movie/${movieId}/videos?api_key=${TMDBAPI}&language=ko-KR`;
    const trailerResponse = await axios.get(trailerUrl);

    //    
    const youtubeLink = trailerResponse.data.results.find(
      (result) => result.site === "YouTube"
    );
    if (!youtubeLink) {
         const trailerUrl = `https://api.themoviedb.org/3/movie/${movieId}/videos?api_key=${TMDBAPI}`;
         const trailerResponse = await axios.get(trailerUrl);

         //   
         const youtubeLink = trailerResponse.data.results.find(
           (result) => result.site === "YouTube"
         );
           const youtubeId = youtubeLink.key;
           return { youtubeId, moivePosterUrl };
    }

    const youtubeId = youtubeLink.key;
    return {youtubeId,moivePosterUrl};
  } catch (error) {
    console.error("TMDB API :", error);
    return null;
  }
}

function YouTubeVideo({ videoId }) {
  const videoUrl = `https://www.youtube.com/embed/${videoId}?autoplay=1`;
  return (
    <div className="youtubevideo">
      <iframe
        width="100%"
        height="50%"
        src={videoUrl}
        allowFullScreen
      ></iframe>
    </div>
  );
}

export default Trailer;
