import { configureStore, createSlice } from "@reduxjs/toolkit";

const boxOfficeRank = createSlice({
  name: "boxOfficeRank",
  initialState: {
    boxOfficeRankData: null,
  },
  reducers: {
    setBoxOfficeRank(state, action) {
      state.boxOfficeRankData = action.payload;
    },
  },
});

const newMovieRank = createSlice({
  name: "newMovieRank",
  initialState: {
    newMovieRankData: null,
  },
  reducers: {
    setNewMovieRank(state, action) {
      state.newMovieRankData = action.payload;
    },
  },
});

export const { setBoxOfficeRank } = boxOfficeRank.actions;
export const { setNewMovieRank } = newMovieRank.actions;

export default configureStore({
  reducer: {
    boxOfficeRank: boxOfficeRank.reducer,
    newMovieRank : newMovieRank.reducer,
  },
});
