import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from "axios";
import "../css/mainlist.css";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Trailer from "./trailer";

const TMDBAPI = "460b0a97023202f5144501426e8e44fc";
function MainList() {


  let mainList = useSelector((state) => state.boxOfficeRank);
  let popluarList = useSelector((state) => state.newMovieRank);
  
   
   
  
  const [boxOfficeList, setBoxOfficeList] = useState("");
  const [posterurl,setPosterurl] = useState("");
  const [tmdbListTitle, setTmdbListTitle] = useState("");
  const [tmdbListPoster,setTmdbListPoster]=useState("");
 
  

  useEffect(() => {
    if (
      mainList &&
      mainList.boxOfficeRankData &&
      mainList.boxOfficeRankData.length > 0
    ) {
       TmdbListPopluarList(
         popluarList.newMovieRankData,
         setTmdbListPoster,
         setTmdbListTitle
       );
      const movieTitle = mainList.boxOfficeRankData.map(
        (movie) => movie.movieNm
      );
      setBoxOfficeList(movieTitle);
    }
  }, [mainList,popluarList]);

  useEffect(()=> {
    const fetchPoster = async () => {
      
      try{
        const posterUrls = await Promise.all (
          boxOfficeList.map(async(title)=> {
            const posterUrl = await getPoster(title,TMDBAPI);
            return posterUrl;
            
          })
        );
        setPosterurl(posterUrls);
      }catch (error){
        console.error("TMDBAPI API  ",error);
      }

    };
    if(boxOfficeList.length>0){
      fetchPoster();
    }

  },[boxOfficeList,TMDBAPI])

  
  const settings = {
    arrows: true,
    infinite: true, //  
    speed: 300, //    (ms)
    slidesToShow: 5, //     
    slidesToScroll: 5, //      
    autoplay: true, //    
    autoplaySpeed: 8000,
    responsive: [
      //  
      {
        breakpoint: 1024, //   1024px    
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
      
        },
      },
      {
        breakpoint: 600, //   600px    
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
    ], 
  };
 
  return (
    <div className="mainlist_container">
      <Trailer></Trailer>
      <h2> </h2>
      <div className="mainlist_box">
        <Slider {...settings}>
          {posterurl &&
            posterurl.map((url, index) => (
              <div key={index} className="mainlist_list">
                <p className="poster_rank">{index + 1}</p>
                <img src={url} alt={`  .`}></img>
                <p className="poster_title">{boxOfficeList[index]}</p>
              </div>
            ))}
        </Slider>
      </div>
      <h2>TMDB </h2>
      <div className="mainlist_box">
        <Slider {...settings}>
          {tmdbListPoster &&
            tmdbListPoster.map((url, index) => (
              <div key={index} className="mainlist_list">
                <p className="poster_rank">{index + 1}</p>
                <img
                  src={`https://image.tmdb.org/t/p/w500${url}`}
                  alt={`  .`}
                ></img>
                <p className="poster_title">{tmdbListTitle[index]}</p>
              </div>
            ))}
        </Slider>
      </div>
    </div>
  );

  
}

const getPoster = async (boxOfficeList, TMDBAPI) => {
  try {
    const response = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=${TMDBAPI}&query=${encodeURIComponent(boxOfficeList)}&language=ko-KR`);
    if (response.data.results.length === 0) {
      //   :      
      const seriesTitle = boxOfficeList.replace(/(\S)\s*$/, " $1");
      const seriesResponse = await axios.get(
        `https://api.themoviedb.org/3/search/movie?api_key=${TMDBAPI}&query=${encodeURIComponent(
          seriesTitle
        )}&language=ko-KR`
      );

      if (seriesResponse.data.results.length === 0) {
        //       
        throw new Error("No poster found.");
      }
      //     URL 
      const posterUrl = `https://image.tmdb.org/t/p/w500${seriesResponse.data.results[0].poster_path}`;
      return posterUrl;
    }
    const posterUrl = `https://image.tmdb.org/t/p/w500${response.data.results[0].poster_path}`;
    return posterUrl;
} catch (error) {
    console.error(" ", error);
    return null;
  }
};

const TmdbListPopluarList = async (test2, setTmdbListPoster,setTmdbListTitle) => {
  if (test2 && test2.length > 0) {
    // test2 null     
    const test3 = test2.map((movie) => movie.title);
    const test4 = test2.map((movie) => movie.poster_path);
    setTmdbListPoster(test4);
    setTmdbListTitle(test3);

  } else {
    console.error("test2 null   .");
  }
};

export default MainList;
