import { useEffect } from "react";
import { useDispatch } from "react-redux";
import axios from "axios";
import { setBoxOfficeRank, setNewMovieRank } from "./store";

const BoxOfficeList = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    const fetchData = async () => {
      try {
        const today = new Date();
        const yesterday = new Date(today);
        yesterday.setDate(today.getDate() - 1);
        const year = yesterday.getFullYear();
        const month = String(yesterday.getMonth() + 1).padStart(2, "0");
        const day = String(yesterday.getDate()).padStart(2, "0");
        const koreaMovieKey = "297449aaa313ceedc08e2b8613c8f8b4";
        const targetDate = `${year}${month}${day}`;
        
        const response = await axios.get(
          `http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json?key=${koreaMovieKey}&targetDt=${targetDate}`
        );
        const boxofficeList = response.data.boxOfficeResult.dailyBoxOfficeList;
        dispatch(setBoxOfficeRank(boxofficeList));
     
      } catch (error) {
        console.error("KOFIC API :", error);
      }
    };
    fetchData();
  }, [dispatch]);

  return null;
};

const NewMovieList = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const TMDBAPI = "460b0a97023202f5144501426e8e44fc";
        const response = await axios.get(
          `https://api.themoviedb.org/3/trending/movie/day?&api_key=${TMDBAPI}&language=ko-KR`
        );
        const tmdbList = response.data.results;
       dispatch(setNewMovieRank(tmdbList));
        //      
      } catch (error) {
        console.error("KOFIC API :", error);
      }
    };

    fetchData();
  }, [dispatch]);

  return null;
};
  

export {BoxOfficeList,NewMovieList};
